
import { onMounted, ref, watch } from "vue";
import ApiService from "../../core/services/ApiService";
import { useI18n } from "vue-i18n";
import { useField } from "vee-validate";

export default {
  name: "CountriesSelect",
  props: {
    country_id: {
      type: String,
      default: "",
    },
    showLabel: {
      type: Boolean,
      default: false,
    },
    size: {
      type: String,
      default: "small",
    },
    required: {
      type: Boolean,
      default: true,
    },
  },
  emits: ["update:country_id"],

  setup(props) {
    const { t } = useI18n();
    const inputCountry = ref({
      loading: false,
      options: [],
      list: [],
    });

    const isCountryRequired = (value) => {
      if (props.required && !value) {
        return t("rcountry");
      }
      return true;
    };

    const { errorMessage, value: element_id } = useField(
      "countries",
      isCountryRequired
    );

    const getCountry = () => {
      ApiService.get("/api/countries").then(({ data }) => {
        inputCountry.value.list = data;
        inputCountry.value.options = data;
      });
    };
    const selectCountry = (query) => {
      if (query !== "") {
        inputCountry.value.loading = true;
        setTimeout(() => {
          inputCountry.value.loading = false;
          inputCountry.value.options = inputCountry.value.list.filter(
            (item: Record<string, any>) => {
              return item.name.toLowerCase().indexOf(query.toLowerCase()) > -1;
            }
          );
        }, 200);
      } else {
        inputCountry.value.options = [];
      }
    };

    watch(
      () => props.country_id,
      (first) => {
        if (first) {
          element_id.value = first;
        }
      }
    );

    onMounted(() => getCountry());

    return {
      element_id,
      inputCountry,
      errorMessage,
      getCountry,
      selectCountry,
    };
  },
};
