
import { defineComponent, onMounted, ref } from "vue";
import { Form, Field, ErrorMessage } from "vee-validate";
import ApiService from "@/core/services/ApiService";
import PortsSelect from "@/components/catalogs-select/PortsPodSelect.vue";
import { useI18n } from "vue-i18n";
import CountriesSelect from "@/components/catalogs-select/CountriesSelect.vue";

interface FormCatalog {
  id?: number;
  country_id: string;
  subdivision_id?: string;
  code: string;
  full_code: string;
  name: string;
  full_name: string;
  coordinates: string;
  iso: number;
  maritime: number;
}

export default defineComponent({
  name: "LocationTypeForm",
  components: {
    CountriesSelect,
    Form,
    Field,
    ErrorMessage,
  },
  props: {
    entityId: String,
  },

  setup(props, { emit }) {
    const { t } = useI18n();
    const submitButton = ref<HTMLElement | null>(null);
    const form = ref<FormCatalog>({
      country_id: "",
      code: "",
      full_code: "",
      name: "",
      full_name: "",
      coordinates: "",
      iso: 0,
      maritime: 0,
    });

    const isNameRequired = (value) => {
      if (!value) {
        return t("rname");
      }
      return true;
    };

    const isIsoRequired = (value) => {
      if (!value) {
        return t("riso");
      }
      return true;
    };

    const isMaritimeRequired = (value) => {
      if (!value) {
        return t("rmaritime");
      }
      return true;
    };

    onMounted(() => {
      if (props.entityId) {
        ApiService.get("/api/location/" + props.entityId).then(({ data }) => {
          form.value = { ...data };
        });
      }
    });

    const createElement = async (resetForm) => {
      try {
        const resp = (await ApiService.post("/api/location", form.value)).data;
        emit("success", resp);
        resetForm();
        submitButton.value?.removeAttribute("data-kt-indicator");
      } catch (error) {
        submitButton.value?.removeAttribute("data-kt-indicator");
      }
    };

    const updateElement = (resetForm) => {
      ApiService.put("/api/location/" + props.entityId, form.value)
        .then(({ data }) => {
          emit("success", data);
          submitButton.value?.removeAttribute("data-kt-indicator");
          form.value = data;
          resetForm();
        })
        .catch(() => {
          submitButton.value?.removeAttribute("data-kt-indicator");
        });
    };

    const onSumbit = (value, { resetForm }) => {
      if (submitButton.value) {
        submitButton.value.setAttribute("data-kt-indicator", "on");
        if (form.value.id) {
          updateElement(resetForm);
        } else {
          createElement(resetForm);
        }
      }
    };

    return {
      submitButton,
      form,
      onSumbit,
      isNameRequired,
      isIsoRequired,
      isMaritimeRequired,
      updateElement,
      createElement,
    };
  },
});
